<template>
<aside id="sidebar" class="sidebar sidebar-active">
  <div class="sidebar-inner">
    <section class="site-overview">
      <div class="site-author">
        <img class="site-author-image" src="../assets/img/zard.jpg">
        <p class="site-author-name">What a beautiful moment...</p>
      </div>
      <div class="liner-split"></div>
      <nav class="menu" @click="closeMenu">
        <ul>
          <li>
            <router-link to="/">Home 首页</router-link>
          </li>
          <li>
            <router-link to="/music">Music 音乐盒</router-link>
          </li>
          <li>
            <router-link to="/gallary">Galary 图库</router-link>
          </li>
          <li>
            <router-link to="/more">More 更多</router-link>
          </li>
        </ul>
      </nav>
    </section>
    <div class="sidebar-close text-center">
      <i class="iconfont icon-tubiao06" @click="closeMenu"></i>
    </div>
  </div>
</aside>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'aside',
  methods: {
    ...mapActions({
      closeMenu: 'TOGGLE_MENU',
    }),
  },
};
</script>

<style lang="scss">
.sidebar {
  position : fixed;
  top: 0;
  right:-80%;
  transition: all ease .3s; 
  background: #000;
  z-index: 10001;
  width: 80%;
  height: 100%;
  .sidebar-inner {
    padding: 20px 0;
  }
  .site-author {
    text-align: center;
    .site-author-image {
      width: 100px;
      height: 100px;
      border: solid 2px #aaa;
    }
    .site-author-name {
      color: #d5d602;
      line-height: 30px;
      text-align: center;
    }
  }
  .liner-split {
    height: 1px;
    background: linear-gradient(to
    left,#000,#fff,#000);
  }
  .menu {
    li {
      list-style: none;
      text-align: center;
      line-height: 60px;
      box-shadow: 0 0 2px #aaa;
    }
  }
  .sidebar-close {
    position: absolute;
    width: 100%;
    bottom: 30px;
  }
  .sidebar-close i {
    font-size: 36px;
    color: #fff;
  }
}
</style>



