import Vue from 'vue';
import Router from 'vue-router';
import Views from '@/components/';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Views.Index,
    },
    {
      path: '/gallary',
      name: 'Gallary',
      component: Views.Gallary,
    },
    {
      path: '/music',
      name: 'Music',
      component: Views.Music,
    },
    {
      path: '/more',
      name: 'More',
      component: Views.More,
    },
  ],
});



