<template>
<div id="app">
  <frame-header />
  <frame-aside :style="asideMenuStyle" />
  <div id="content" :style="contentStyle">
    <div class="container">
      <transition name="fade-transition" mode="out-in">
        <keep-alive>
        <router-view></router-view>
        </keep-alive>
      </transition>
    
    </div>
  </div>
  <!-- Footer START -->
  <div id="footer">
    <p class="text-center">©ZARD 2017 | Telanx</p>
  </div>
</div>
</template>

<script>
import { mapGetters } from 'vuex';
import FrameHeader from '@/components/FrameHeader';
import FrameAside from '@/components/FrameAside';

const audio = document.createElement('audio');
export default {
  name: 'app',
  components: {
    FrameHeader,
    FrameAside,
  },
  data() {
    return {
      contentStyle: '',
      audio,
    };
  },
  computed: {
    ...mapGetters({
      menu_expanded: 'menu_expanded_status',
    }),
    asideMenuStyle() {
      const x = this.menu_expanded ? '-100%' : '0%';
      return `transform: translateX(${x})`;
    },
  },
  mounted() {
    window.vm = this;
    const ch = document.documentElement.clientHeight - 120;
    this.contentStyle = `min-height:${ch}px`;
  },
};
</script>

<style lang="scss">
/**引入全局样式 */
@import './assets/css/base.scss';
#content {
  background: rgb(20,139,205);
  padding-top: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#footer {
  width: 100%;
  height: 40px;
  background: #fff;
  box-shadow: 0 -2px 5px rgba(0,0,0,.2);
  p {
    line-height: 40px;
  }
}
</style>



